import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import Components from "views/Components/Components.js";
import LandingPage from "views/LandingPage/LandingPage.js";
import ProfilePage from "views/ProfilePage/ProfilePage.js";
import LoginPage from "views/LoginPage/LoginPage.js";
import BookOnlinePage from "views/BookOnlinePage/BookOnlinePage.js";
import StudioPage from "views/StudioPage/StudioPage.js";
import ShopPage from "views/ShopPage/ShopPage.js";
import ContactPage from "views/ContactPage/ContactPage.js";
import BlogPage from "views/BlogPage/BlogPage.js";
import MaterialKitGlobal from "views/Components/MaterialKitGlobal.js";


var hist = createBrowserHistory();

ReactDOM.render(
  <Router history={hist}>
    <Switch>

      <Route path="/landing-page" component={LandingPage} />
      <Route path="/about-me" component={ProfilePage} />
      <Route path="/login-page" component={LoginPage} />
      <Route path="/materialKit" component={MaterialKitGlobal} />
      <Route path="/book-online" component={BookOnlinePage} />
      <Route path="/spa" component={StudioPage} />
      <Route path="/shop" component={ShopPage} />
      <Route path="/contact-us" component={ContactPage} />
      <Route path="/blog" component={BlogPage} />
      <Route path="/" component={Components} />
        {/*This needs to be at the end / root path*/}

    </Switch>
  </Router>,
  document.getElementById("root")
);
